<template>
<v-img src="/img/Mountain-top-rocks-trees-sunrise-fog_1920x1200.jpg" style="position: fixed">
  <v-app  style="background: transparent " >
    <v-content>
      <v-container
        class="fill-height pa-0"
        fluid
      >
        <v-row
          align="center"
          justify="center"
          style="height: 100%"
        >
          <v-col
          class="pa-0  center-element"
            cols="12"
            sm="9"
            md="4"

          >
            <v-card   flat  style="width: 100%" color="transparent" class="px-6">
       
              <v-card-text  >
                <v-form  v-model="valid" ref="form">
                  <v-text-field
                  outlined
                   :disabled="loading"
                     v-model="username"
                    label="Username"
                    name="login"
                    autocomplete="username"
                    :rules="rules.username"
                    prepend-icon="mdi-account"
                    type="text"
                    required
                    @keypress.enter="submit"
                  ></v-text-field>

                  <v-text-field
                    outlined
                   :disabled="loading"
                    v-model="password"
                    label="Password"
                    name="password"
                    autocomplete="current-password"
                    prepend-icon="mdi-lock"
                    type="password"
                    :rules="rules.password"
                    required
                    @keypress.enter="submit"
                  ></v-text-field>
                </v-form>
             
              </v-card-text>
              <v-card-actions  >
                <v-btn :loading="loading"  v-if="$route.query.reg" x-large color="green" @click="submit($event, true)" :disabled="!valid || loading">register</v-btn>
                <v-spacer></v-spacer>
                <v-btn :loading="loading" x-large color="primary" @click="submit" :disabled="!valid || loading" >Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        
        <v-col></v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</v-img>
</template>
<script>
 
  import { mapActions } from 'vuex'
  export default {
    name: 'Login',
    data() {
      return {
        loading: false,
        valid: false,
        username: '',
        password: '',
        rules: {
          username: [v => !!v || 'Username is required', /*v => v.length >= 5 || 'Username must be more or equal to 5 characters'*/],
          password: [v => !!v || 'Password is required', v => v.length >= 6 || 'Password must be more or equal to 6 characters']
        }
        
      }
    },
    methods: {
      ...mapActions({
        register: 'auth/register',
        login: 'auth/login'
      }),
       submit(evt, register = false) {
        evt.preventDefault()
        this.loading = true
        if (this.$refs.form.validate()) {
          const { username, password } = this
          let fn = register ?  this.register( { username, password } ) :  this.login( { username, password } );
          fn.catch(err=> {
            console.log(err);
            this.$store.commit('error', err.response.data)
          })
        } else {
            this.$store.commit('error', 'INVALID FORM PLEASE CHECK THE FORM AND TRY AGAIN!')
        }
        this.loading = false

      } 
    }
  }
</script>
<style scoped>
.center-element {
  display: flex;
  align-items: center;
  height: 100%;
  background: rgba(255, 255, 255, 0.85);
}
</style>